
.team {
  background-color: #e6f0ff ;
  padding: 4rem;
}

.team__inner {
  max-width: 800px;
  margin: 0 auto;

  @include display(flex);
  @include flex-wrap(wrap);
}


.team.cards .team__member {
  width: 50%;
  padding: 2rem;
  text-align: center;
  @include transition(all 0.6s);


  h2, h4, p {
    color: $blue;
    text-shadow: 1px 1px rgba(#fff, 0.5);
  }

  h4 {
    font-weight: 400;
    font-size: 1.1rem;
  }

  p {
    padding: 0.5rem 0 0 0;
    font-size: 1.2rem;
    font-weight: 100;
  }

  .small {
    font-size: 1rem;
  }

  @include max-screen(768px) {
    width: 100%;
  }

  &:hover {
    background-color: #fff;
    box-shadow: 0 6px 18px 0 rgba(black, 0.1);
  }
}



