
.offer {
  padding: 10rem 4rem;

  &__inner {
    width: 60%;
    margin: auto;

    @include max-screen(640px) {
      width: 80%;
    }

    @include max-screen(480px) {
      width: 100%;
    }
  }
}


.offer h2 {
  text-align: center;
  font-size: 2.5rem;
  color: $blue;
  padding-bottom: 2rem;
}

.offer h4 {
  font-weight: 300;
  font-size: 1.7rem;
  padding-top: 2rem;
}

.offer p {
  font-weight: 100;
  font-size: 1.15rem;
  line-height: 1.6;
}




